<template>
	<view>
		<x-custom v-if="theme === 'custom'" />
		<!-- <x-default v-if="theme === 'default'" /> -->
		<view class="wall-wrap" v-if="theme === 'default'">
			<view class="" v-if="list.length > 0">
				<view class="art-item" v-for="(item, index) in list" :key="index">
					<view class="content" @click="handleCopy(item, index)">{{ item.content }}</view>
					<view class="act-btn" @click="handleCopy(item, index)">
						<image src="../../static/copy.png" mode="aspectFit"></image>
						<text class="copy">复制</text>
						<text>{{ item.copyNum }}</text>
					</view>
				</view>
				<uni-load-more :status="status" :content-text="contentText"></uni-load-more>
			</view>
			<x-empty v-else />
		</view>
	</view>
</template>
<script>
import xCustom from '../custom/index.vue';
import xEmpty from '@/components/xEmpty.vue';
// import xDefault from '../default/index.vue';
export default {
	components: {
		xCustom,
		xEmpty
		// xDefault
	},
	data() {
		return {
			theme: '',
			status: 'more',
			initLoad: false,
			from: {
				page: 1,
				pageSize: 10
			},
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '加载中...',
				contentnomore: '我是有底线的'
			},
			list: [],
			resList: []
		};
	},
	async onShow() {
		await uni.hideTabBar();
		await this.getTheme();
		this.initLoad = true;
		this.list = [];
		this.getData();
		setTimeout(function() {
			console.log('start pulldown');
		}, 300);
		uni.startPullDownRefresh();
	},
	onReachBottom() {
		console.log('onReachBottom');
		if (this.theme === 'custom') {
			uni.stopPullDownRefresh();
		}

		this.initLoad = false;
		if (this.resList.length < 10) {
			this.status = 'noMore';
			return;
		}
		this.status = 'loading';
		setTimeout(() => {
			if (this.resList.length === 10) {
				this.from.page++;
			}
			this.getData();
		}, 300);
	},
	async onPullDownRefresh() {
		console.log('onPullDownRefresh');
		if (this.theme === 'custom') {
			uni.stopPullDownRefresh();
		}
		this.from.page = 1;
		this.getData();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 300);
	},
	watch: {
		theme: {
			immediate: true,
			handler(val) {
				if (val === 'default') {
					uni.setNavigationBarTitle({
						title: '寄语语录'
					});
				} else if (val === 'custom') {
					uni.setNavigationBarTitle({
						title: '写寄语'
					});
					uni.stopPullDownRefresh();
				} else {
					uni.setNavigationBarTitle({
						title: '寄语语录'
					});
				}
			}
		}
	},
	methods: {
		getTheme() {
			this.$api.wall.fetchTheme().then(res => {
				// console.log(res)
				this.theme = res.data;
			});
		},
		// 获取数据
		async getData() {
			const {
				code,
				result: { data }
			} = await this.$api.wall.fetchList(this.from);
			if (code === 200) {
				this.resList = data;
				let formatList = [];

				if (this.list.length === 0) {
					this.list = data;
				} else {
					this.list = this.list.map(item => {
						return data.find(i => i._id === item._id) || item;
					});
					let ids = this.list.map(item => item._id);
					formatList = data.filter(item => !ids.includes(item._id));
				}
				// 第一次加载新数据加在前面
				if (this.initLoad) {
					this.list = (this.list.length === 0 ? data : formatList).concat(this.list);
				} else {
					this.list = this.list.concat(this.list.length === 0 ? data : formatList);
				}
				if (data.length === 0) {
					this.status = 'noMore';
				}
			}
		},
		// 复制数
		async handleCopy(row, index) {
			const { content, _id } = row;
			let obj = {
				...row,
				copyNum: row.copyNum++
			};
			const { code, message } = await this.$api.wall.editWall(obj);
			if (code === 200) {
				uni.setClipboardData({
					data: content,
					success: function() {
						//调用方法成功
						console.log('success');
						this.from.page = Math.floor(parseInt(index) / 10) + 1;
						this.getData();
					}
				});
			}
		}
	}
};
</script>
<style></style>
